<template>
  <div class="head">
    <van-nav-bar :title="title" left-text="" :class="{ active: headerActive }" left-arrow>
      <template #left v-if="Headtype === 'back'">
        <img class="back" src="" @click="$goBack" alt="" />
      </template>
      <template #left v-else>
        <img v-show="!open" class="header-icon" src="@/assets/images/more-close.png" @click="onMenu" alt="" />
        <img v-show="open" class="header-icon" src="@/assets/images/more-open@2x.png" @click="onMenu" alt="" />
      </template>
    </van-nav-bar>
    <!-- 菜单栏 -->
    <div class="menu-Block" v-show="open" @click.self="closeMenu()">
      <div class="menu-leftBlock">
        <div class="menu-block">
          <div class="block">
            <div class="title">{{ $t("index.我的錢包") }}</div>
            <div class="wallet-block flex-center">
              <span v-if="address" class="address flex-center">
                {{ address }}
                <!-- {{address.substring(0, 4)}}***{{address.substring(address.length - 3, address.length)}} -->
                <img class="copy" :data-clipboard-text="address" src="@/assets/images/copy@2x.png" alt="" />
              </span>
              <span v-else class="address flex" @click="initWallet">
                {{ $t("web3.连接钱包") }}
              </span>
            </div>
          </div>
        </div>
        <div class="menu-item flex-middle">
          <img class="menu-img" src="@/assets/images/lang@2x.png" />
          <div class="menu-tit" v-if="local === 'en'" @click="changeLanuage('zh-TW')">
            English
          </div>
          <div class="menu-tit" v-else @click="changeLanuage('en')">
            繁體中文
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import Clipboard from "clipboard";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
import { onBeforeRouteLeave } from "vue-router";
import { Toast } from "vant";
import { config } from "@/utils/config";
import stakeAbi from "@/utils/stakeAbi.json";
export default {
  name: "HeadTop",
  props: {
    Headtype: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    }
  },
  setup (props) {
    const store = useStore();
    const { t } = useI18n();
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      isPlegdeOut: computed(() => store.state.isPlegdeOut),
      dialogMessage: computed(() => store.state.dialogMessage),
      open: false,
      Headtype: props.Headtype,
      headerActive: 0,
      title: props.title,
      local: getLang().toLowerCase() || "zh-TW",
      curOrigin: location.origin,
      stakeContract: null,
      changeLanuage: val => {
        setLang(val);
        data.local = val;
        location.reload()
      },
      initContract: () => {
        data.stakeContract = new data.web3.eth.Contract(
          stakeAbi,
          config.pledgeAddres
        );
        data.initData();
      },
      initData: () => {
      },
      initWallet: () => {
        web3Data.initWeb3();
      },
      onMenu: () => {
        data.open = !data.open;
      },
      openUrl: url => {
        window.location.href = url;
      },
      closeMenu: () => {
        data.open = false;
      }
    });
    onMounted(() => {
      var clipboard = new Clipboard(".copy");
      clipboard.on("success", function (e) {
        Toast(t("share.複製成功"));
        e.clearSelection();
      });
      clipboard.on("error", function () {
        Toast(t("share.複製失敗，請手動長按複製"));
      });
      var clipboardHref = new Clipboard(".copy-cur-href");
      clipboardHref.on("success", function (e) {
        Toast(t("share.複製成功"));
        e.clearSelection();
      });
      clipboardHref.on("error", function () {
        Toast(t("share.複製失敗，請手動長按複製"));
      });
    });
    // document.addEventListener("scroll", function () {
    //   let scrollTop =
    //     document.documentElement.scrollTop || document.body.scrollTop;
    //   data.headerActive = scrollTop > 30;
    // });
    if (data.address) {
      data.initContract();
    }
    watch(
      () => data.address,
      val => {
        if (val) {
          data.initContract();
        }
      }
    );
    onBeforeRouteLeave(() => {
      document.querySelector("body").style.overflow = "auto";
      data.open = false;
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less" scoped>
.wallet-block {
  border: 1px solid #d2d9e1;
  border-radius: 5px;
  padding: 6px 12px;
  width: 100%;
  .wallet-img {
    width: 19px;
    height: 19px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .address {
    overflow: hidden;
    font-size: 14px;
    color: #111;
    word-break: break-all;
  }
}
.menu-Block {
  position: fixed;
  left: 0;
  right: 0;
  top: 60px;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 499;
  .menu-leftBlock {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background: #fff;
    right: 31%;
    .menu-item {
      padding: 14px 14px;
      .menu-img {
        width: 21px;
        height: 21px;
      }
      .menu-tit {
        font-size: 14px;
        color: #222222;
        margin-left: 20px;
        span {
          font-size: 16px;
          color: #ff9600;
        }
      }
    }
  }
}
.back {
  width: 10px !important;
  height: 18px;
}
.header-icon {
  width: 23px;
  height: 23px;
  display: block;
}
.copy {
  width: 16px;
  height: 16px;
}
.menu-block {
  padding: 0 15px;
  .title {
    font-size: 15px;
    color: #111;
    font-weight: 600;
    margin-bottom: 10px;
    margin-top: 13px;
  }
}
.intro-wrap {
  .address {
    color: #0ac067;
  }
  .wallet-block {
    border-color: #0ac067;
  }
}
</style>
