<template>
  <div class="container manage">
    <HeadTop :title="$t('index.合約管理')"></HeadTop>
    <div class="input-card border" v-for="(item, index) in inputData" :key="index">
      <div class="title">
        {{ index + 1 }}.{{ $t(`index.${item.title}`) }}
        <van-loading v-if="item.isSearch" type="spinner" size="18px" style="margin-left:4px;" />
      </div>
      <template v-if="item.key === 'amountOut'">
        <div class="input-wrap">
          <input v-model.trim="item.input.address" type="text" :placeholder="$t('index.地址')" />
        </div>
        <div class="input-wrap">
          <input v-model.trim="item.input.amount" type="text" :placeholder="$t('index.數量')" />
        </div>
      </template>
      <template v-else-if="item.key === 'plan'">
        <div class="ul">
          <div class="li flex">
            <div class="lable">{{ $t("index.方案編號") }}</div>
            <div class="input-wrap">
              <input v-model.trim="item.input.modeId" @blur="queryStakeModeHandle(item)" type="number" :placeholder="$t('index.請輸入')" />
            </div>
          </div>
          <div class="li flex">
            <div class="lable">{{ $t("index.USDT比例") }}</div>
            <div style="width:202px;">
              <div class="input-wrap">
                <input v-model.trim="item.input.usdtPCT" type="number" :placeholder="$t('index.請輸入')" />
                <div class="lable">‰</div>
              </div>
              <div class="tip">{{ $t("index.注：80%請輸入800") }}</div>
            </div>
          </div>
          <div class="li flex">
            <div class="lable">{{ $t("index.封頂倍數") }}</div>
            <div style="width:202px;">
              <div class="input-wrap">
                <input v-model.trim="item.input.multiple" type="number" :placeholder="$t('index.請輸入')" />
              </div>
              <div class="btn-wrap">
                <van-button class="on" v-if="!item.input.disabled" @click="item.input.disabled = true" type="primary">{{ $t("index.啟用") }}</van-button>
                <van-button class="off" v-else type="primary" @click="item.input.disabled = false">{{ $t("index.禁用") }}</van-button>
              </div>
            </div>
          </div>
        </div>
      </template>
      <template v-else>
        <div class="input-wrap">
          <input v-model.trim="item.input.value" type="text" :readonly="item.input.readonly" :placeholder="$t('index.請輸入')" />
          <div class="lable" v-if="item.input.lable">
            {{ item.input.lable }}
          </div>
        </div>
      </template>
      <div class="btn-wrap" v-if="item.key !== 'addressContract'">
        <template v-if="item.key === 'amountIn' && Number(allowanceMole) <= 0">
          <van-button type="primary" @click="approveMoleHandle(item)" :disabled="item.isSearch || item.btnLoading" :loading="item.btnLoading" :loading-text="$t('Approve') + '...'">{{ $t("Approve") }}</van-button>
        </template>
        <template v-else>
          <van-button type="primary" @click="submitHandle(item)" :disabled="item.isSearch || item.btnLoading" :loading="item.btnLoading" :loading-text="$t('index.保存') + '...'">{{ $t("index.保存") }}</van-button>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from "@/components/HeadTop";
import { getCurrentInstance, reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { config } from "@/utils/config";
import stakeAbi from "@/utils/stakeAbi.json";
import moleAbi from "@/utils/moleAbi.json";
import { Toast } from "vant";
import BigNumber from "bignumber.js";
import { MaxUint256 } from "@ethersproject/constants";
export default {
  components: {
    HeadTop
  },
  setup () {
    const internalInstance = getCurrentInstance();
    const store = useStore();
    const { t } = useI18n();
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      stakeContract: null,
      moleContract: null,
      allowanceMole: 0,
      inputData: [
        {
          key: "setFuncAddress",
          title: "結算go程序地址（調用地址）",
          input: { value: "" },
          btnLoading: false
        },
        {
          key: "setPriceAddress",
          title: "更新價格go程序地址",
          input: { value: "" },
          btnLoading: false
        },
        {
          key: "addressContract",
          title: "Mole合約地址",
          input: { value: config.moleAddress, readonly: true }
        },
        {
          key: "addressDestroy",
          title: "銷毀地址",
          input: { value: "" },
          btnLoading: false
        },
        {
          key: "unstakeFee",
          title: "解押手續費比例",
          input: { value: "", lable: "‰" },
          btnLoading: false
        },
        {
          key: "feeRealse",
          title: "解押手續費MOL轉入地址",
          input: { value: "" },
          btnLoading: false
        },
        {
          key: "feeWithdraw",
          title: "提取收益手續費",
          input: { value: "", lable: "BNB" },
          btnLoading: false
        },
        {
          key: "amountIn",
          title: "轉入MOL數量",
          input: { value: "", lable: "MOL" },
          btnLoading: false
        },
        {
          key: "amountOut",
          title: "轉出token",
          input: { address: "", amount: "" },
          btnLoading: false
        },
        {
          key: "plan",
          title: "設置方案",
          input: { modeId: "", usdtPCT: "", multiple: "", disabled: false },
          btnLoading: false,
          isSearch: false
        }
      ],
      StakeModeIds: [],
      initContract: () => {
        data.stakeContract = new data.web3.eth.Contract(
          stakeAbi,
          config.pledgeAddres
        );
        data.moleContract = new data.web3.eth.Contract(
          moleAbi,
          config.moleAddress
        );
        data.initData();
      },
      initData: () => {
        data.FuncAddressHandle();
        data.getPriceAddressHandle();
        data._burnAddressHandle();
        data.getMarketAddressHandle();
        data.getAllowanceMole();
        data.getGasAmountHandle();
        data.getUnStakeFeeHandle();
      },
      submitHandle: item => {
        let { key } = item;
        // console.log(key)
        switch (key) {
          case "setFuncAddress":
            data.setFuncAddressHandle(item);
            break;
          case "setPriceAddress":
            data.setPriceAddressHandle(item);
            break;
          case "addressDestroy":
            data.setBurnAddressHandle(item);
            break;
          case "feeRealse":
            data.setMarketAddressHandle(item);
            break;
          case "feeWithdraw":
            data.setGasPriceHandle(item);
            break;
          case "amountIn":
            data.depositManagerHandle(item);
            break;
          case "amountOut":
            data.withdrawManagerHandle(item);
            break;
          case "plan":
            data.setStakeModeHandle(item);
            break;
          case "unstakeFee":
            data.setUnstakeFeeHandle(item);
            break;
          default:
            break;
        }
      },
      setFuncAddressHandle: async item => {
        // console.log(item)
        item.btnLoading = true;
        try {
          await data.stakeContract.methods
            .setFuncAddress(item.input.value)
            .estimateGas({
              from: data.address
            });
          await data.stakeContract.methods
            .setFuncAddress(item.input.value)
            .send({ from: data.address });
          item.btnLoading = false;
          Toast(t("index.Success"));
        } catch (error) {
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      setPriceAddressHandle: async item => {
        console.log(item)
        item.btnLoading = true;
        try {
          await data.stakeContract.methods
            .setPriceADdress(item.input.value)
            .estimateGas({
              from: data.address
            });
          await data.stakeContract.methods
            .setPriceADdress(item.input.value)
            .send({ from: data.address });
          item.btnLoading = false;
          Toast(t("index.Success"));
        } catch (error) {
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      setBurnAddressHandle: async item => {
        item.btnLoading = true;
        if (!data.web3.utils.isAddress(item.input.value)) {
          item.btnLoading = false;
          return Toast(t("index.不是有效的以太坊地址"));
        }
        try {
          await data.stakeContract.methods
            .setBurnAddress(item.input.value)
            .estimateGas({
              from: data.address
            });
          await data.stakeContract.methods
            .setBurnAddress(item.input.value)
            .send({ from: data.address });
          Toast(t("index.Success"));
          item.btnLoading = false;
        } catch (error) {
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      setMarketAddressHandle: async item => {
        item.btnLoading = true;
        if (!data.web3.utils.isAddress(item.input.value)) {
          item.btnLoading = false;
          return Toast(t("index.不是有效的以太坊地址"));
        }
        try {
          await data.stakeContract.methods
            .setMarketAddress(item.input.value)
            .estimateGas({
              from: data.address
            });
          await data.stakeContract.methods
            .setMarketAddress(item.input.value)
            .send({ from: data.address });
          Toast(t("index.Success"));
          item.btnLoading = true;
        } catch (error) {
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      setGasPriceHandle: async item => {
        item.btnLoading = true;
        try {
          let number = item.input.value || 0;
          number = new BigNumber(number).multipliedBy(10 ** 18).toFixed(0);
          await data.stakeContract.methods.setGasPrice(number).estimateGas({
            from: data.address
          });
          // console.log(number, "setGasPrice");
          await data.stakeContract.methods
            .setGasPrice(number)
            .send({ from: data.address });
          item.btnLoading = false;
          Toast(t("index.Success"));
        } catch (error) {
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      setUnstakeFeeHandle: async item => {
        item.btnLoading = true;
        let fee = item.input.value;
        // console.log(fee)
        try {
          await data.stakeContract.methods.setUnstakeFee(fee).estimateGas({
            from: data.address
          });
          await data.stakeContract.methods
            .setUnstakeFee(fee)
            .send({ from: data.address });
          item.btnLoading = false;
          Toast(t("index.Success"));
        } catch (error) {
          // console.log(error)
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      depositManagerHandle: async item => {
        item.btnLoading = true;
        // console.log(item)
        try {
          let decimals = await data.moleContract.methods
            .decimals()
            .call({ from: data.address });
          let number = new BigNumber(item.input.value)
            .multipliedBy(10 ** decimals)
            .toFixed();
          // console.log(number, decimals, 'depositManager')
          await data.stakeContract.methods.depositManager(number).estimateGas({
            from: data.address
          });
          await data.stakeContract.methods
            .depositManager(number)
            .send({ from: data.address });
          item.btnLoading = false;
          Toast(t("index.Success"));
        } catch (error) {
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      withdrawManagerHandle: async item => {
        item.btnLoading = true;
        // console.log(item)
        let _address = item.input.address;
        if (!data.web3.utils.isAddress(_address)) {
          item.btnLoading = false;
          return Toast(t("index.不是有效的以太坊地址"));
        }
        try {
          let decimals = await data.moleContract.methods
            .decimals()
            .call({ from: data.address });
          let amount = new BigNumber(item.input.amount)
            .multipliedBy(10 ** decimals)
            .toFixed();
          // console.log(amount, decimals, _address, 'withdrawManager')
          await data.stakeContract.methods
            .withdrawManager(_address, amount)
            .estimateGas({
              from: data.address
            });
          await data.stakeContract.methods
            .withdrawManager(_address, amount)
            .send({ from: data.address });
          item.btnLoading = false;
          Toast(t("index.Success"));
        } catch (error) {
          // console.log(error)
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      queryStakeModeHandle: async item => {
        item.isSearch = true;
        let _modeId = item.input.modeId;
        let StakeModeIds = [];
        try {
          StakeModeIds = await data.stakeContract.methods
            .getStakeModeIds()
            .call({ from: data.address });
          // console.log(StakeModeIds, "StakeModeIds");
        } catch (error) {
          // console.log(error)
          item.isSearch = false;
        }
        if (StakeModeIds.indexOf(_modeId) !== -1) {
          try {
            // console.log(typeof _modeId, _modeId, "getStakeModeById");
            let res = await data.stakeContract.methods
              .getStakeModeById(_modeId)
              .call({ from: data.address });
            // console.log(res, "getStakeModeById");
            item.isSearch = false;
            let { modeId, usdtPCT, multiple, disabled } = res;
            item.input = { modeId, usdtPCT, multiple, disabled };
            // console.log(item)
          } catch (error) {
            item.isSearch = false;
            // console.log(error)
          }
        } else {
          let { modeId } = item.input;
          item.input = { modeId, usdtPCT: "", multiple: "", disabled: false };
          item.isSearch = false;
        }
      },
      setStakeModeHandle: async item => {
        try {
          item.btnLoading = true;
          let { modeId, multiple, usdtPCT, disabled } = item.input;
          if (Number(modeId) <= 0) {
            item.btnLoading = false;
            return Toast("方案編號不能為0");
          }
          // console.log(modeId, [modeId, usdtPCT, multiple, disabled]);
          await data.stakeContract.methods
            .setStakeMode(modeId, [modeId, usdtPCT, multiple, disabled])
            .estimateGas({
              from: data.address
            });
          await data.stakeContract.methods
            .setStakeMode(modeId, [modeId, usdtPCT, multiple, disabled])
            .send({ from: data.address });
          // console.log(res)
          item.btnLoading = false;
          Toast(t("index.Success"));
        } catch (error) {
          // console.log(error)
          item.btnLoading = false;
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      findIndex: (key, callback) => {
        let _inputData = data.inputData
        for (let index = 0; index < _inputData.length; index++) {
          if (key === _inputData[index].key) {
            return callback(index)
          }
        }
      },
      FuncAddressHandle: async () => {
        data.findIndex('setFuncAddress', async (index) => {
          try {
            let res = await data.stakeContract.methods
              .funcAddress()
              .call({ from: data.address });
            data.inputData[index].input.value = res;
          } catch (error) {
            data.inputData[index].input.value = "";
          }
        })
      },
      getPriceAddressHandle: async () => {
        data.findIndex('setPriceAddress', async (index) => {
          try {
            let res = await data.stakeContract.methods
              .priceAddress()
              .call({ from: data.address });
            data.inputData[index].input.value = res;
          } catch (error) {
            data.inputData[index].input.value = "";
          }
        })
      },
      _burnAddressHandle: async () => {
        data.findIndex('addressDestroy', async (index) => {
          try {
            let res = await data.stakeContract.methods
              ._burnAddress()
              .call({ from: data.address });
            data.inputData[index].input.value = res;
          } catch (error) {
            data.inputData[index].input.value = "";
          }
        })
      },
      getMarketAddressHandle: async () => {
        data.findIndex('feeRealse', async (index) => {
          try {
            let res = await data.stakeContract.methods
              .getMarketAddress()
              .call({ from: data.address });
            // console.log(res, 'getMarketAddress')
            data.inputData[index].input.value = res;
          } catch (error) {
            data.inputData[index].input.value = "";
          }
        })
      },
      getGasAmountHandle: async () => {
        data.findIndex('feeWithdraw', async (index) => {
          try {
            let res = await data.stakeContract.methods
              ._gasAmount()
              .call({ from: data.address });
            data.inputData[index].input.value = new BigNumber(res).dividedBy(
              10 ** 18
            );
          } catch (error) {
            data.inputData[index].input.value = 0;
          }
        })
      },
      getUnStakeFeeHandle: async () => {
        data.findIndex('unstakeFee', async (index) => {
          try {
            let res = await data.stakeContract.methods
              .getUnstakeFee()
              .call({ from: data.address });
            // console.log(res, 'getUnStakeFeeHandle')
            data.inputData[index].input.value = res;
          } catch (error) {
            data.inputData[index].input.value = "";
            internalInstance.appContext.config.globalProperties.$errorformat(
              error
            );
          }
        })
      },
      getAllowanceMole: async () => {
        let allowance = await data.moleContract.methods
          .allowance(data.address, config.pledgeAddres)
          .call({ from: data.address });
        // console.log(allowance, "getAllowanceMole");
        data.allowanceMole = allowance;
        return allowance;
      },
      approveMoleHandle: () => {
        data.moleContract.methods
          .approve(config.pledgeAddres, MaxUint256)
          .send({ from: data.address })
          .then(async () => {
            // console.log(res, 'approveUsdtHandle')
            let allowance = await data.getAllowanceMole();
            // console.log(allowance, 'allowance')
            if (allowance) {
              Toast(t("Approve success"));
            } else {
              Toast(t("Approve fail"));
            }
          })
          .catch(() => {
            Toast(t("Approve fail"));
          });
      }
    });
    if (data.address) {
      data.initContract();
    }
    watch(
      () => data.address,
      val => {
        if (val) {
          data.initContract();
        }
      }
    );
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less" scoped>
.input-card {
  background: #eff4fa;
  padding: 0 12px 20px;
  margin-bottom: 15px;
  .title {
    font-size: 15px;
    color: #111;
    font-weight: 600;
    padding: 17px 0 10px;
    display: flex;
  }
  .ul {
    .li {
      display: flex;
      justify-content: space-between;
      align-content: center;
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
      .tip {
        font-size: 13px;
        color: #666;
        text-align: right;
        padding-top: 8px;
      }
      .lable {
        line-height: 37px;
        flex-shrink: 0;
        font-size: 15px;
        color: #666;
      }
      .btn-wrap {
        margin-top: 12px;
        justify-content: flex-end;
        .van-button {
          height: 37px;
          font-size: 15px;
          width: 50%;
          &.on {
            background: #37bf5a;
            border-color: #37bf5a;
          }
          &.off {
            background: #ff4a2c;
            border-color: #ff4a2c;
          }
          // &:nth-child(1):first-child {
          //   background: #37bf5a;
          //   border-color: #37bf5a;
          // }
          // &:nth-child(2):last-child {
          //   background: #ff4a2c;
          //   border-color: #ff4a2c;
          // }
        }
      }
      .input-wrap {
        width: 202px;
        flex-shrink: 0;
      }
    }
  }
  .input-wrap {
    border-radius: 5px;
    background: #fff;
    display: flex;
    align-items: center;
    & + .input-wrap {
      margin-top: 15px;
    }
    .lable {
      font-size: 15px;
      color: #111;
      padding: 0 10px;
    }
    input {
      border: 0;
      outline: 0;
      width: 100%;
      font-size: 15px;
      color: #111;
      height: 37px;
      padding: 0 12px;
      border-radius: 5px;
    }
  }
}
</style>
<style lang="less"></style>
